/* global Plotly:true */

import React from 'react';
//import createPlotlyComponent from 'react-plotly.js/factory'
import Plot from 'react-plotly.js';
import Select from 'react-select';
import makeAnimated from 'react-select/lib/animated';

import '../../styles/Resizer.css';

/* JSON Editor styling */
import '../../styles/autocomplete.css';
import '../../styles/contextmenu.css';
import '../../styles/jsoneditor.css';
import '../../styles/menu.css';
import '../../styles/reset.css';
import '../../styles/searchbox.css';
import { chain, zipObject, get, pick } from 'lodash';
import Loader from 'react-loader-spinner'

import './PlotifyGraph.css';
import Filter from '../filter/Filter';


class PlotifyGraph extends React.Component {
  constructor(props) {
    super(props);
    this.handleJsonChange = this.handleJsonChange.bind(this);

    this.state = {
      json: null,
      plotUrl: '',
      dimenssions: [],
      measures: [],
      measure: '',
      dimenssion: '',
      isLoader: false,
      data: [],
      fields: [],
      dataToGraph: []
    };
    //this.loadCsv('./data/india.csv');
    this.loadCsv('./data/twitter-fewvalues.csv');
  }

  handleJsonChange = newJSON => {
    this.setState({ json: newJSON });
  }

  getRandomColor() {
    var letters = '0123456789ABCDEF';
    var color = '#';
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  loadCsv(path) {
    Plotly
      .d3
      .csv(path, (err, rows) => {
        if (err) {
          console.log(`Datasource CS file ${path} has not loaded`, err);
          return;
        } else {
          console.log(`csv data ${rows}`);
        }
        let dimenssions = [];
        let measures = [];
        const object = rows[0];
        for (const key in object) {
          if (object.hasOwnProperty(key) && ((key !== 'user_id') && key.indexOf('color') === -1)) {
            const element = object[key];
            if (isNaN(parseInt(element))) {
              dimenssions.push(key);
            } else {
              measures.push(key);
            }
          }
        }
        const fields = Object.keys(object).map((key) => {
          return {
            value: key,
            label: key,
            name: key,
            type: isNaN(parseInt(object[key])) ? 'string' : 'number'
          };
        });

        dimenssions = dimenssions.map((o) => {
          return {
            label: o,
            value: o
          };
        });
        measures = measures.map((o) => {
          return {
            label: o,
            value: o
          };
        });
        this.setState({
          data: rows,
          dimenssions: dimenssions,
          measures: measures,
          fields: fields
        });

      });
  }

  prepareData() {
    const timeSeries = [];
    const unpack = (rows, key) => {
      return rows.map(function (row) { return isNaN(parseInt(row[key])) ? 0 : parseInt(row[key]); });
    };
    const groupedData = chain(this.state.data)
      .groupBy((item) => {
        return item[this.state.dimenssion];
      }).toPairs()
      .map((currentItem) => {
        return zipObject([this.state.dimenssion, "items"], currentItem);
      })
      .value();
    for (const key in groupedData) {
      if (groupedData.hasOwnProperty(key)) {
        const groupedItems = groupedData[key];
        const y = unpack(groupedItems.items, this.state.measure);
        console.log(y);
        const series = {
          type: "scatter",
          mode: "lines",
          name: groupedItems[this.state.dimenssion],
          x: new Date(),
          y: y,
          line: { color: this.getRandomColor() }
        };
        timeSeries.push(series);
      }
    }
    const layout = {
      title: 'Basic Time Series',
      xaxis: {
        rangeselector: {
          buttons: [
            {
              count: 15,
              label: '15min',
              step: 'minute',
              stepmode: 'backward'
            },
            {
              count: 30,
              label: '30min',
              step: 'minute',
              stepmode: 'backward'
            },
            {
              count: 45,
              label: '45min',
              step: 'minute',
              stepmode: 'backward'
            },
            {
              count: 1,
              label: '1h',
              step: 'hour',
              stepmode: 'backward'
            },
            {
              count: 1,
              label: '1d',
              step: 'day',
              stepmode: 'backward'
            },
            {
              count: 1,
              label: '1m',
              step: 'month',
              stepmode: 'backward'
            },
            {
              count: 6,
              label: '6m',
              step: 'month',
              stepmode: 'backward'
            },
            {
              count: 1,
              label: '1y',
              step: 'year',
              stepmode: 'backward'
            },
            { step: 'all' }
          ]
        },
        title: 'Time Series',
        type: 'date',
        //rangeslider: {range: ['2015-02-17', '2019-09-16']},
      }
    };

    //Plotly.newPlot('myDiv', data, layout, { showSendToCloud: true });

    const plotJSON = {
      data: timeSeries,
      layout: layout
    };

    this.setState({
      json: plotJSON
    });
  }

  onFilterChange = (filters) => {
    this.setState({
      measure: get(filters, 'measure.value', ''),
      dimenssion: get(filters, 'dimenssion.value', '')
    });

    if (filters.measure && filters.dimenssion) {
      this.setLoader(true);
      setTimeout(() => {
        this.prepareData();
      }, 0);
    }
    console.log('filters', filters);
  }

  onFieldChange = (field) => {
    const fieldsName = field.map((f) => f.name);
    const data1 = this.state.data.map((i) => {
      return pick(i, fieldsName);
    });
    console.log(data1);
  }

  setLoader = (flag) => {
    this.setState({
      isLoader: flag
    });
  }


  render() {
    return (
      <div className="row">
        <div className="col-md-4" style={{ maxWidth: '300px' }}>
          <div className='controls-panel'>
            <Filter dimenssions={this.state.dimenssions} measures={this.state.measures} onFilterChange={this.onFilterChange} />
          </div>
        </div>
        <div className="col">
          {
            this.state.isLoader && <div style={{ position: 'absolute', left: '45%', top: '35%' }}>
              <Loader
                type="ThreeDots"
                color="#00BFFF"
                height="100"
                width="100"
              />
            </div>
          }
          {this.state.json && <Plot
            data={this.state.json.data}
            layout={this.state.json.layout}
            config={{ displayModeBar: true, responsive: true }}
            style={{ width: '100%' }}
            onInitialized={() => this.setLoader(false)}
          />}
        </div>
      </div>
    );
  }
}

export default PlotifyGraph;
