import React from 'react';
import {
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap';

import './Header.css';

class AppHeader extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    return (
      <div>
        <header className="header-top">
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-4">
                <a href="logo" className="logo-img text-white" >Gaphify
            </a>
              </div>
              <div className="col-md-8 d-flex align-items-center justify-content-end">
                <ul className="top no-style-list d-flex mb-0 ">
                  <li>
                    <a href="#" target="_blank" rel="noopener noreferrer" className="documentation  text-white text-uppercase font-weight-bold">
                      <i className="custom-color fa fa-clone mr-2" aria-hidden="true"></i>
                      Documentation</a></li>
                  <li><a href="#" target="_blank" rel="noopener noreferrer" className="Setting text-white text-uppercase"><i className="mr-2 custom-color fa fa-cog" aria-hidden="true"></i>Settings</a></li>
                  <li className="border-icon"><a href="#" target="_blank" rel="noopener noreferrer" className=" text-white text-uppercase font-weight-bold">
                    <i className="icon-bell fa fa-bell" aria-hidden="true"></i></a></li>
                  <UncontrolledDropdown nav inNavbar>
                    <DropdownToggle nav caret>
                      option
                </DropdownToggle>
                    <DropdownMenu right>
                      <DropdownItem>
                        Account
                  </DropdownItem>


                      <DropdownItem>
                        Logout
                  </DropdownItem>
                    </DropdownMenu>
                  </UncontrolledDropdown>


                </ul>
              </div>
            </div>
          </div>
        </header>




        <header className="header-inner">
          <div className="container-fluid">

            <div className="row">
              <div className="col-md-12 p-0">
                <ul className="navigation d-inline-flex align-items-center no-style-list m-0 p-0">
                  <li className="workplace text-uppercase" >All Workspaces</li>
                  <li className="ml-5  mt-2 "><a className="selected active  text-uppercase font-weight-bold" href="#" aria-current="page">Pivot</a></li>
                  <li className="ml-5 "><a className="text-uppercase  text-white " href="#" aria-current="page">Data Resources</a></li>
                  <li className="ml-5 "><a className="text-uppercase  text-white " href="#" aria-current="page">Materilized View</a></li>
                  <li className="ml-5 "><a className="text-uppercase  text-white " href="#" aria-current="page">Outputs</a></li>
                  <li className="ml-5 "><a className="text-uppercase  text-white " href="#" aria-current="page">Clusters</a></li>
                </ul>
              </div>
            </div>
          </div>



        </header>

      </div>
    );
  }
}

export default AppHeader;