import React, { Fragment } from 'react';
import './App.css';
import AppHeader from './components/header/Header';
import DatasourceInfo from './components/datasourceInfo/DatasourceInfo';
import PlotifyGraph from './components/plotifyGraph/PlotifyGraph';
import PivotTableComponent from './components/pivotTable/PivotTable';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  render() {
    return (
      <Fragment key="app">
         <AppHeader></AppHeader>
         <DatasourceInfo></DatasourceInfo>
         <PlotifyGraph></PlotifyGraph>
         <div style={{marginTop:'50px'}}>
         <PivotTableComponent></PivotTableComponent>
         </div>
      </Fragment>
    );
  }
}
export default App;
