import React, { Fragment } from 'react';
import AsyncSelect from 'react-select/lib/Async';
import Select from 'react-select';

import './Filter.css';

const aggregations = [
  { value: 'count', label: 'Count' },
  { value: 'sum', label: 'Sum' },
  { value: 'avarage', label: 'Avarage' },
  { value: 'min', label: 'Min' },
  { value: 'max', label: 'Max' }
];
const label = {
  marginBottom: '8px',
  display: 'block',
  fontWeight: '500'
};

class Filter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dimenssion: null,
      aggregation: null,
      measure: null
    }
  }
  handleChange(option, field) {
    this.setState({
      [field]: option
    });
    setTimeout(()=>{
      this.props.onFilterChange(this.state);
    },0);
  }


  render() {
    const { dimenssion, aggregation, measure } = this.state;
    const { dimenssions,measures } = this.props;
    return (
      <Fragment key="graphify-filter">
        <div className="control-item" style={{ margin: '20px 0' }}>
          <label style={label}>Dimension</label>
          <Select
            name="dimenssion"
            value={dimenssion}
            isSearchable={true}
            onChange={(e) => this.handleChange(e, 'dimenssion')}
            options={dimenssions}
          />
        </div>
        <div className="control-item" style={{ margin: '20px 0' }}>
          <label style={label}>Aggregation</label>
          <Select
            name="aggregation"
            value={aggregation}
            onChange={(e) => this.handleChange(e, 'aggregation')}
            options={aggregations}
          />
        </div>
        <div className="control-item" style={{ margin: '20px 0' }}>
          <label style={label}>Measure</label>
          <Select
            name="measure"
            value={measure}
            onChange={(e) => this.handleChange(e, 'measure')}
            options={measures}
          />
        </div>
      </Fragment>
    );
  }
}
export default Filter;
