import React from 'react';
import './DatasourceInfo.css';
import {
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap';


class DatasourceInfo extends React.Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  render() {
    return (
      <div>
        <div className="datasource">
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-4">
                <h1><span>Twitter Datasoure</span></h1>
                <p> <a href="#"
                  target="_blank" rel="noopener noreferrer">Learn more about Data Source Discovery.</a></p>
              </div><div className="col-md-8">
                <ul className="definition-list no-style-list d-flex align-items-center justify-content-end">
                  <li><strong><span>Cluster</span></strong><span className="limited">Running</span></li>
                  <li><strong>Total Events</strong><span>70</span></li>
                  <li><strong>Parse Errors</strong><span>0</span></li>
                  <UncontrolledDropdown inNavbar>
                    <DropdownToggle nav caret>
                      Add output
                </DropdownToggle>
                    <DropdownMenu right>
                      <DropdownItem>
                        Add Dashboard
                  </DropdownItem>
                      <DropdownItem>
                        Add Materialized View
                  </DropdownItem>
                      <DropdownItem>
                        Archive Data Source
                  </DropdownItem>
                    </DropdownMenu>
                  </UncontrolledDropdown>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <hr></hr>
        <ul className="tabs-react no-style-list mb-3 p-0">
          <li><a>Schema</a></li>
          <li><a>Samples</a></li>
          <li><a>Parse Errors</a></li>
          <li><a>Properties</a></li>
          <li><a>Used By (0)</a></li>
        </ul>
      </div>
    );
  }
}

export default DatasourceInfo;